<template>
  <div class="page flex-box">
    <el-card>
      <el-form :model="form" ref="form">
        <el-form-item prop="phone">
          <el-input v-model.trim="form.accessKey" placeholder="Access Key" />
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            v-model.trim="form.secretKey"
            type="password"
            placeholder="Secret Key"
          />
        </el-form-item>
        <el-form-item>
          <el-button @click="authorize" block>授权</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { AuthorizeForm } from "../assets/script/model"
import storageUtil from "../assets/script/util/storage"
export default {
  name: "SignIn",
  data() {
    return {
      form: new AuthorizeForm()
    }
  },
  methods: {
    authorize() {
      storageUtil.sessionSet("accessKey", this.form.accessKey)
      storageUtil.sessionSet("secretKey", this.form.secretKey)
      this.$router.push("/")
    }
  }
}
</script>

<style type="text/less" lang="less" scoped>
.page {
  height: 100vh;
}
</style>
